/* eslint-disable prefer-destructuring */
import Axios from "axios";

const instance = Axios.create();

let token;
let sfdcorgid;
let authorization;

if (process.env.REACT_APP_SFDCORGID && process.env.REACT_APP_SFDCORGID !== "") {
  sfdcorgid = process.env.REACT_APP_SFDCORGID;
  authorization = process.env.REACT_APP_BASIC_TOKEN;
} else {
  token = window.__grax__.token;
  sfdcorgid = window.__grax__.orgid;
  authorization = (window.__grax__.pageParams || {}).authorization;
}

instance.interceptors.request.use(config => {
  const configAuth = { ...config };

  configAuth.headers.sfdcorgid = sfdcorgid;
  configAuth.withCredentials = true;

  if (authorization != null && authorization !== "") {
    configAuth.headers.Authorization = `Basic ${authorization}`;
  } else if (token && token !== "") {
    configAuth.headers.Authorization = `Bearer ${token}`;
  }

  return configAuth;
});

export default instance;
