import {
  QUICK_START_FORM_SET_PROVIDER,
  SALESFORCE_CONNECTION_SET_VALUE,
  QUICK_START_FORM_INITIAL_LOAD
} from "../../consts/configurations/configurationsActions";
import InvalidActionTypeError from "../../models/errors/InvalidActionTypeError";

const salesforceConnectionReducer = (state, { type, payload, meta }) => {
  const selectedTab = state.provider;

  switch (type) {
    case QUICK_START_FORM_INITIAL_LOAD:
      return {
        ...state,
        [selectedTab]: payload
      };
    case QUICK_START_FORM_SET_PROVIDER:
      return {
        ...state,
        provider: payload.provider
      };
    case SALESFORCE_CONNECTION_SET_VALUE:
      const dataSource = state[selectedTab][meta.variant];

      return {
        ...state,
        [selectedTab]: {
          ...state[selectedTab],
          [meta.variant]: {
            provider: selectedTab,
            ...dataSource,
            ...payload
          }
        }
      };
    default:
      throw new InvalidActionTypeError();
  }
};

export default salesforceConnectionReducer;
