import { useEffect, useState } from "react";

import _ from "lodash";

import REGION_LABEL_MAP from "../../consts/configurations/regionLabelMap";
import vendorCredentialWhitelist from "../../consts/configurations/vendorCredentialWhitelist.json";
import CloudConfig from "../../models/CloudConfig";

/**
 * Returns displayable form of vendor credentials for configuration summary page
 *
 */
const useConfigurationSummary = configuration => {
  const [summary, setSummary] = useState({});

  useEffect(() => {
    if (configuration) {
      const cloudConfig = new CloudConfig(configuration);
      const { elastic, storage } = cloudConfig;
      const salesforce = cloudConfig.sourceDataSource;

      const summaryData = createSummaryData([
        { ...elastic, type: "elastic" },
        { ...storage, type: "storage" },
        { ...salesforce, type: "salesforce" }
      ]);

      setSummary(summaryData);
    }
  }, [configuration]);

  return summary;
};

/**
 * Returns displayable form of vendor credentials
 *
 * @param { Array } credentialsArray array of credentials to process
 */
export const createSummaryData = credentialsArray => {
  const results = {};

  credentialsArray.forEach(credentialsSet => {
    results[credentialsSet.type] = _displayableProperties(credentialsSet);
  });

  return results;
};

/**
 * Converts vendor credentials into displayable format for Configuration Summary page,
 * including filtering out secure credentials, substituting readable names for json values,
 * and capitalizing properly.
 *
 * Display order is determined by vendor credentials whitelist file
 * @param { Object } {type, ...credentials} credentials with type (elastic, storage, salesforce)
 * @returns { Object } formatted and filtered vendor credentials
 */
const _displayableProperties = ({ type, ...credentials }) => {
  const displayCredentials = [];
  const userKeys = Object.keys(credentials);

  const whitelist = vendorCredentialWhitelist[type];
  const whitelistedCredentials = whitelist.map(entry => entry.jsonName);

  whitelistedCredentials.forEach(credentialName => {
    if (userKeys.includes(credentialName)) {
      const translationPair = whitelist.filter(entry => entry.jsonName === credentialName)[0];
      const label = translationPair.displayName;
      const content = _formatContent(credentialName, credentials[credentialName]);
      displayCredentials.push({ label, content, truncate: true });
    }
  });
  return displayCredentials;
};

/**
 * Performs any capitalization or punctuation cleanup
 *
 * @param { String } credentialName name of credential to format
 * @param { String } credentialValue value of credential to format
 * @returns { String } formatted credential value for display
 */
const _formatContent = (credentialName, credentialValue) => {
  if (credentialName === "provider") {
    return credentialValue === "aws" ? "AWS" : _.upperFirst(credentialValue);
  }
  if (credentialName === "region") {
    return REGION_LABEL_MAP[credentialValue];
  }
  return _.toString(credentialValue);
};

export default useConfigurationSummary;
