import React from "react";
import PropTypes from "prop-types";

import useSaveBasicConfiguration from "../../../../hooks/configuration/useSaveBasicConfiguration";
import HeaderButtons from "../HeaderButtons";

const propTypes = {
  formRef: PropTypes.shape({ current: PropTypes.object }).isRequired
};

const HeaderActions = ({ formRef }) => {
  const onSubmit = useSaveBasicConfiguration();

  return <HeaderButtons formRef={formRef} onSubmit={onSubmit} />;
};

HeaderActions.propTypes = propTypes;
export default HeaderActions;
