import {
  CONNECTION_PROVIDERS_OAUTH,
  CONNECTION_PROVIDERS_SFDC
} from "../../consts/configurations/connectionProviders";
import { SALESFORCE_CONNECTION_SET_VALUE } from "../../consts/configurations/configurationsActions";

import { useSalesforceConnectionContext } from "../../contexts/salesforceConnectionContext";
import { useHandleInput } from "../useHandleForm";
import useSelectedProvider from "./useSelectedProvider";

const options = [
  {
    label: "Username / Password",
    value: CONNECTION_PROVIDERS_SFDC
  }
];

/**
 * Accesses corresponding context to retrieve/set provider data
 *
 * @returns { Array } [{selectedTab, provider, options}, {dispatch}]
 */

const useSalesforceConnection = (variant = "sourceDataSource") => {
  const [state, dispatch] = useSalesforceConnectionContext();
  const dataSources = useSelectedProvider(state);
  const onMetaBlur = useHandleInput(SALESFORCE_CONNECTION_SET_VALUE, dispatch);
  const onBlur = event => onMetaBlur(event, { meta: { variant } });

  const { userName, password, token, url, name, organizationId } = dataSources[variant] || {};
  const selectedTab = state.provider;

  return [
    {
      selectedTab,
      userName,
      password,
      token,
      url,
      name,
      organizationId,
      options
    },
    { onBlur, dispatch }
  ];
};

export default useSalesforceConnection;
