import { createContext } from "react";

import { withQuickStartProvider } from "./contextProviders";
import { useStateContext, useDispatchContext } from "../hooks/configuration/useContextProvider";
import salesforceConnectionReducer from "../reducers/configurations/salesforceConnectionsReducer";

import {
  CONNECTION_PROVIDERS_OAUTH,
  CONNECTION_PROVIDERS_SFDC
} from "../consts/configurations/connectionProviders";

const initialState = {
  provider: CONNECTION_PROVIDERS_SFDC,
  [CONNECTION_PROVIDERS_SFDC]: {
    sourceDataSource: {},
    syncSalesforceEinstein: {},
    bulkRestoreDestination: {}
  },
  [CONNECTION_PROVIDERS_OAUTH]: {}
};

const stateContext = createContext();
const dispatchContext = createContext();

export const useSalesforceConnectionContext = () => [
  useStateContext(stateContext),
  useDispatchContext(dispatchContext)
];

export const SalesforceConnectionProvider = withQuickStartProvider(
  stateContext,
  dispatchContext,
  initialState,
  salesforceConnectionReducer
);
