import {
  PUSH_TO_QUEUE_PROVIDER_APIGEE,
  PUSH_TO_QUEUE_PROVIDER_KAFKA,
} from '../../consts/configurations/pushToQueueProvider';
import { SALESFORCE_GIT_INTEGRATION_GITLAB } from '../../consts/configurations/sfdcGitIntegration';
import { useCloudStorageContext } from '../../contexts/cloudStorageContext';
import { useConfigurationsContext } from '../../contexts/configurationsContext';
import { useEnvironmentVariablesContext } from '../../contexts/environmentVariablesContext';
import { useMetadataBackupContext } from '../../contexts/metadataBackupContext';
import { useSalesforceConnectionContext } from '../../contexts/salesforceConnectionContext';
import { useSyncAnalyticsContext } from '../../contexts/syncAnalyticsContext';
import CloudConfig from '../../models/CloudConfig';
import useSelectedProvider from './useSelectedProvider';
import useValidateAndSaveConfiguration from './useValidateAndSaveConfiguration';

const omitEmptyProps = obj => {
  if (obj !== null && typeof obj === "object") {
    return Object.entries(obj).reduce((accumulator, [key, value]) => {
      const omittedEmptyProps = { ...accumulator };
      if (value !== "" && value !== undefined && value !== null) {
        omittedEmptyProps[key] = obj[key];
      }
      return omittedEmptyProps;
    }, {});
  }

  return obj;
};

export const preValidateDataSourcesSchema = (dataSources, newDataSource) => {
  const { type, provider, organizationId, ...credential } = newDataSource;
  const isValidConnection = Object.keys(omitEmptyProps(credential)).length > 0;

  if (isValidConnection === true) return [...dataSources, newDataSource];

  return dataSources;
};

const preValidateMetadataBackupSchema = metadataBackup => {
  const { isEnabled, sourceControlEnabled, sourceControlProvider } = metadataBackup;
  const schema = [];

  if (!isEnabled || !sourceControlEnabled) {
    schema.push({ isEnabled: !!isEnabled, sourceControlEnabled: false });
  } else if (sourceControlProvider === SALESFORCE_GIT_INTEGRATION_GITLAB) {
    const { gitUserId, ...omittedGitUserId } = metadataBackup;
    schema.push(omitEmptyProps(omittedGitUserId));
  } else {
    schema.push(omitEmptyProps(metadataBackup));
  }

  return schema;
};

const preValidatePushToQueueSchema = pushToQueue => {
  const schema = [];

  if (pushToQueue.provider === PUSH_TO_QUEUE_PROVIDER_KAFKA) {
    const { provider, authorizationToken, apiEndpoint } = pushToQueue;
    schema.push(omitEmptyProps({ provider, authorizationToken, apiEndpoint }));
  } else if (pushToQueue.provider === PUSH_TO_QUEUE_PROVIDER_APIGEE) {
    const { provider, apiClientId, apiEndpoint, apiClientSecret } = pushToQueue;
    schema.push(omitEmptyProps({ provider, apiClientId, apiEndpoint, apiClientSecret }));
  } else {
    schema.push({});
  }

  return schema;
};

export const preValidateVariablesSchema = variables => {
  let schema = variables;

  if (variables.cdcEnabled !== undefined) {
    schema = {
      ...schema,
      cdcEnabled: variables.cdcEnabled.toString()
    };
  }

  return omitEmptyProps(schema);
};

const useSaveAdvancedConfiguration = () => {
  const [configuration, configurationDispatch] = useConfigurationsContext();
  const [storageState] = useCloudStorageContext();
  const [metadataBackup] = useMetadataBackupContext();
  const [syncAnalytics] = useSyncAnalyticsContext();
  const [dataSources] = useSalesforceConnectionContext();
  const destinations = useSelectedProvider(dataSources);
  const storage = useSelectedProvider(storageState);
  const [
    { pushToQueue, timeMachine, variables, sessionCacheTimeoutMinutes, asyncIntervalMinutes }
  ] = useEnvironmentVariablesContext();
  const saveConfiguration = useValidateAndSaveConfiguration(configurationDispatch);

  return () => {
    const cloudConfig = new CloudConfig(configuration);

    if (syncAnalytics.isEinsteinAnalyticsEnabled === true) {
      cloudConfig.dataSources = preValidateDataSourcesSchema(
        cloudConfig.dataSources,
        destinations.syncSalesforceEinstein
      );
    }
    cloudConfig.dataSources = preValidateDataSourcesSchema(
      cloudConfig.dataSources,
      destinations.bulkRestoreDestination
    );
    cloudConfig.metadataBackup = preValidateMetadataBackupSchema(metadataBackup);
    cloudConfig.pushToQueue = preValidatePushToQueueSchema(pushToQueue);
    cloudConfig.variables = preValidateVariablesSchema(variables);
    cloudConfig.storage = omitEmptyProps(storage);
    cloudConfig.syncAnalytics = omitEmptyProps(syncAnalytics);
    cloudConfig.timeMachine = omitEmptyProps(timeMachine);
    cloudConfig.asyncIntervalMinutes = asyncIntervalMinutes;
    cloudConfig.sessionCacheTimeoutMinutes = sessionCacheTimeoutMinutes;
    saveConfiguration(cloudConfig.configuration);
  };
};

export default useSaveAdvancedConfiguration;
