import { createRef } from 'react';

import { useConfigurationsContext } from '../../contexts/configurationsContext';
import CloudConfig from '../../models/CloudConfig';

const useAdvancedConfiguration = () => {
  const [state] = useConfigurationsContext();
  const formRef = createRef();

  const cloudConfig = new CloudConfig(state);
  const {
    asyncIntervalMinutes,
    sessionCacheTimeoutMinutes,
    storage,
    metadataBackup,
    syncAnalytics,
    variables,
    timeMachine,
    pushToQueue,
    syncSalesforceEinsteinOrganizationId
  } = cloudConfig;

  const dataSources = {
    syncSalesforceEinstein: {
      ...cloudConfig.syncSalesforceEinsteinDataSources,
      type: "syncSalesforceEinstein"
    },
    bulkRestoreDestination: {
      ...cloudConfig.bulkRestoreDestinationDataSource,
      type: "bulkRestoreDestination"
    }
  };

  const environmentVariables = {
    asyncIntervalMinutes,
    sessionCacheTimeoutMinutes,
    variables,
    timeMachine,
    pushToQueue
  };

  return {
    dataSources,
    storage,
    metadataBackup,
    syncAnalytics,
    environmentVariables,
    syncSalesforceEinsteinOrganizationId,
    formRef
  };
};

export default useAdvancedConfiguration;
