import React from "react";
import PropTypes from "prop-types";
import AwsRegions from "../../../../commons/AwsRegions";
import StatefulInput from "../../../../commons/StatefulInput";
import EnableOAuth from "./EnableOAuth";
import StatefulCheckbox from "../../../../commons/StatefulCheckbox";
import ALL_REGIONS from "../../../../../consts/regions";

const propTypes = {
  endpoint: PropTypes.string,
  bucket: PropTypes.string,
  accessKeyName: PropTypes.string,
  secretAccessKey: PropTypes.string,
  enableOAuth: PropTypes.bool,
  clientId: PropTypes.string,
  tokenUrl: PropTypes.string,
  clientSecret: PropTypes.string,
  region: PropTypes.oneOf([...ALL_REGIONS, ""]),
  onBlur: PropTypes.func.isRequired,
  handleCheckbox: PropTypes.func.isRequired,
  handleCombobox: PropTypes.func.isRequired
};

const defaultProps = {
  endpoint: "",
  bucket: "",
  accessKeyName: "",
  secretAccessKey: "",
  enableOAuth: false,
  clientId: "",
  tokenUrl: "",
  clientSecret: "",
  region: ""
};

const S3Compatible = ({
  endpoint,
  bucket,
  accessKeyName,
  secretAccessKey,
  enableOAuth,
  clientId,
  tokenUrl,
  clientSecret,
  region,
  onBlur,
  handleCheckbox,
  handleCombobox
}) => (
  <>
    <div className="slds-grid slds-gutters_small">
      <div className="slds-col slds-size_1-of-3">
        <StatefulInput label="Endpoint" required value={endpoint} id="endpoint" onBlur={onBlur} />
      </div>
      <div className="slds-col slds-size_1-of-3">
        <StatefulInput label="S3 Bucket Name" required value={bucket} id="bucket" onBlur={onBlur} />
      </div>
      <div className="slds-col slds-size_1-of-3">
        <AwsRegions
          label="S3 Region"
          value={region}
          onChange={selections => handleCombobox("region", selections)}
        />
      </div>
    </div>
    <div className="slds-grid slds-gutters_small">
      <div className="slds-col slds-size_1-of-3">
        <StatefulInput
          label="Access Key ID"
          required
          secured
          value={accessKeyName}
          id="accessKey"
          onBlur={onBlur}
        />
      </div>
      <div className="slds-col slds-size_1-of-3">
        <StatefulInput
          label="Secret Access Key"
          required
          secured
          value={secretAccessKey}
          id="secretAccessKey"
          onBlur={onBlur}
        />
      </div>
      <div className="slds-col slds-size_1-of-3">
        <StatefulCheckbox
          label="Enable OAuth"
          checked={enableOAuth}
          id="enableOAuth"
          variant="toggle"
          isDestructive
          onToggle={handleCheckbox}
        />
      </div>
    </div>
    {enableOAuth === true && (
      <EnableOAuth
        clientId={clientId}
        tokenUrl={tokenUrl}
        clientSecret={clientSecret}
        onBlur={onBlur}
      />
    )}
  </>
);

S3Compatible.propTypes = propTypes;
S3Compatible.defaultProps = defaultProps;

export default S3Compatible;
