import { useReducer, useEffect } from "react";

import { useHistory } from "react-router-dom";
import INITIAL_STATE from "../../consts/configurations/initialState";
import { ENDPOINT_CONFIGURATION, ROUTE_CONFIGURATION } from "../../consts/addresses";
import { CONFIGURATION_LOAD_INITIAL_STATE } from "../../consts/configurations/configurationsActions";
import configurationsReducer from "../../reducers/configurations/configurationsReducer";
import useFetch from "../useFetch";

/**
 * Retrieves configuration from cloud
 *
 * @returns { Array } state (configuration and fetch), configuration dispatch function
 */
export const useConfigurationRetrieve = () => {
  const history = useHistory();
  const [{ data, error }] = useFetch({ url: ENDPOINT_CONFIGURATION });
  const [state, dispatch] = useReducer(configurationsReducer, INITIAL_STATE);

  useEffect(() => {
    if (data !== null) dispatch({ type: CONFIGURATION_LOAD_INITIAL_STATE, payload: data });
  }, [data]);

  useEffect(() => {
    if (error !== null && error === "No Configuration Found") {
      history.push(`${ROUTE_CONFIGURATION}/error`);
    }
  }, [error]);

  return [state, dispatch];
};

/**
 * Saves configuration to cloud
 *
 * @returns { Array } state (configuration and fetch), configuration dispatch function
 */

export const useSaveConfiguration = () => {
  const [saveStatus, setFetch] = useFetch();

  const saveConfiguration = configuration =>
    setFetch({ url: ENDPOINT_CONFIGURATION, method: "POST", data: configuration });

  return [saveStatus, saveConfiguration];
};
