import React from "react";
import PropTypes from "prop-types";

import withProtectedExpandableSection from "./withProtectedExpandableSection";
import ProtectedInput from "./ProtectedInput";

const propTypes = {
  ATTACHMENT_BATCH_SIZE: PropTypes.string.isRequired,
  ATTACHMENT_PROCESSING_SECONDS_PER_RECORD: PropTypes.string.isRequired,
  onBlur: PropTypes.func.isRequired
};

const AttachmentSettings = ({
  ATTACHMENT_BATCH_SIZE,
  ATTACHMENT_PROCESSING_SECONDS_PER_RECORD,
  onBlur
}) => {
  return (
    <div className="slds-grid slds-gutters_small">
      <ProtectedInput
        label="Attachment Batch Size"
        helpText="Used by async attachment process to grab this many attachments at a size (default = 5000)"
        variableName="ATTACHMENT_BATCH_SIZE"
        value={ATTACHMENT_BATCH_SIZE}
        type="number"
        onBlur={onBlur}
      />
      <ProtectedInput
        label="Attachment Processing Seconds Per Record"
        helpText="Used for attachment processing expiry time calculations (default = 5 seconds)"
        variableName="ATTACHMENT_PROCESSING_SECONDS_PER_RECORD"
        value={ATTACHMENT_PROCESSING_SECONDS_PER_RECORD}
        onBlur={onBlur}
      />
    </div>
  );
};

AttachmentSettings.propTypes = propTypes;

export default withProtectedExpandableSection("Attachment Settings")(AttachmentSettings);
