import { useHistory } from "react-router-dom";

import {
  ROUTE_BASIC_CONFIGURATION,
  ROUTE_CONFIGURATION,
  ROUTE_ADVANCED_CONFIGURATION
} from "../consts/addresses";

const useCloudRoutes = () => {
  const history = useHistory();

  return {
    goToSummaryRoute: () => history.push(ROUTE_CONFIGURATION),
    goToBasicConfigurationRoute: () => history.push(ROUTE_BASIC_CONFIGURATION),
    goToAdvancedConfigurationRoute: () => history.push(ROUTE_ADVANCED_CONFIGURATION)
  };
};

export default useCloudRoutes;
