import React, { Fragment, useEffect } from "react";
import PropTypes from "prop-types";

import { Button } from "@salesforce/design-system-react";
import StatefulInput from "../StatefulInput";
import useIndividualVendorValidity from "../../../hooks/configuration/useIndividualVendorValidity";

const propTypes = {
  userName: PropTypes.string,
  password: PropTypes.string,
  token: PropTypes.string,
  url: PropTypes.string,
  name: PropTypes.string,
  organizationId: PropTypes.string.isRequired,
  onBlur: PropTypes.func.isRequired,
  validationDispatch: PropTypes.func.isRequired
};

const SalesforceLogin = ({
  name,
  userName,
  password,
  token,
  url,
  organizationId,
  onBlur,
  onValidated
}) => {
  const [validation, validateVendor] = useIndividualVendorValidity();

  useEffect(() => onValidated(validation), [validation]);

  const onValidation = () =>
    validateVendor({
      type: "salesforce",
      credentials: {
        provider: "selectedTab",
        userName,
        password,
        token,
        url,
        name,
        organizationId
      }
    });

  return (
    <Fragment>
      <div className="slds-grid slds-gutters_small">
        <div className="slds-col slds-size_1-of-3">
          <StatefulInput
            label="SFDC Username"
            required
            value={userName}
            id="userName"
            onBlur={onBlur}
          />
        </div>
        <div className="slds-col slds-size_1-of-3">
          <StatefulInput
            label="SFDC Password"
            required
            secured
            value={password}
            id="password"
            onBlur={onBlur}
          />
        </div>
        <div className="slds-col slds-size_1-of-3">
          <StatefulInput
            label="SFDC Security Token"
            required
            secured
            value={token}
            id="token"
            onBlur={onBlur}
          />
        </div>
      </div>
      <div className="slds-grid slds-gutters_small">
        <div className="slds-col slds-size_1-of-3">
          <StatefulInput
            label="SFDC URL"
            helpText="https://login.salesforce.com for Production  |  https://test.salesforce.com for Sandbox"
            required
            value={url}
            id="url"
            onBlur={onBlur}
          />
        </div>
        {organizationId && (
          <div className="slds-col slds-size_1-of-3">
            <StatefulInput
              readOnly
              label="SFDC Organization ID (18 Digit)"
              value={organizationId}
              id="organizationId"
              onBlur={onBlur}
            />
          </div>
        )}
      </div>
      <div className="slds-grid slds-gutters_small slds-m-top_small">
        <div className="slds-col slds-size_1-of-3">
          <Button label="Validate SFDC Connection" variant="outline-brand" onClick={onValidation} />
        </div>
      </div>
    </Fragment>
  );
};

SalesforceLogin.propTypes = propTypes;

export default SalesforceLogin;
