import React from "react";
import PropTypes from "prop-types";
import { useHistory } from "react-router-dom";
import {
  Button,
  PageHeader,
  PageHeaderControl,
  Icon,
  Card,
  ScopedNotification
} from "@salesforce/design-system-react";

import {
  ROUTE_BASIC_CONFIGURATION,
  ROUTE_ADVANCED_CONFIGURATION
} from "../../../../consts/addresses";
import BasicConfigurationSummary from "./BasicConfigurationSummary";

const propTypes = {
  configuration: PropTypes.object.isRequired
};

const ConfigurationSummary = ({ configuration }) => {
  const history = useHistory();
  const redirect = path => history.push(path);

  const actionsBasic = () => (
    <PageHeaderControl>
      <Button label="Update Credentials" onClick={() => redirect(ROUTE_BASIC_CONFIGURATION)} />
    </PageHeaderControl>
  );

  const actionsAdvanced = () => (
    <PageHeaderControl>
      <Button
        label="Edit Advanced Configuration"
        onClick={() => redirect(ROUTE_ADVANCED_CONFIGURATION)}
      />
    </PageHeaderControl>
  );

  const iconBasic = (
    <Icon
      assistiveText={{ label: "Required Configuration" }}
      category="standard"
      name="calibration"
    />
  );

  const iconAdvanced = (
    <Icon assistiveText={{ label: "Advanced Configuration" }} category="standard" name="custom" />
  );

  return (
    <div className="summaryCard">
      <Card hasNoHeader>
        <PageHeader
          onRenderActions={actionsBasic}
          title="Required Configuration"
          variant="record-home"
          icon={iconBasic}
        />
        <BasicConfigurationSummary configuration={configuration} />
      </Card>
      <Card hasNoHeader>
        <PageHeader
          onRenderActions={actionsAdvanced}
          title="Advanced Configuration"
          variant="record-home"
          icon={iconAdvanced}
        />
        <Card hasNoHeader>
          <ScopedNotification theme="dark">
            <p>Only advanced users should edit these settings.</p>
          </ScopedNotification>
        </Card>
      </Card>
    </div>
  );
};

ConfigurationSummary.propTypes = propTypes;

export default ConfigurationSummary;
