import React from "react";

import { ConfigurationsProvider } from "../../../contexts/configurationsContext";
import { InteractiveFetchStatusProvider } from "../../../contexts/interactiveFetchStatusContext";
import InteractiveFetchStatus from "../../commons/InteractiveFetchStatus";
import NestedRoutes, { NestedRoutesPropTypes } from "../../commons/NestedRoutes";

const Configuration = ({ routes }) => {
  return (
    <ConfigurationsProvider>
      <InteractiveFetchStatusProvider>
        <InteractiveFetchStatus />
        <NestedRoutes routes={routes} />
      </InteractiveFetchStatusProvider>
    </ConfigurationsProvider>
  );
};

Configuration.propTypes = NestedRoutesPropTypes;

export default Configuration;
